.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
}

.expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}

.expense-item h2 {
    color: #FFFCF2;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
}

.expense-item__price {
    font-size: 1rem;
    font-weight: medium;
    border-radius: 17px;
    color: white;
    background-color: #EB5E28;
    box-shadow: 2px 2px 6px #b44519, -2px -2px 6px #b15734;
    /* box-shadow: 2px 2px 6px #2f2e2b, -2px -2px 6px #504d48; */

    padding: 0.5rem;
    border-radius: 10px;
}

@media (min-width: 580px) {
    .expense-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }

    .expense-item__description h2 {
        font-size: 1.25rem;
    }

    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}